import React, { Component } from 'react';

class AddUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            name: '',
            tel: '',
            permisson: '',
        }
    }
    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState = ({
            [name]: value
        });

        var item = {}
        item.id = this.state.id;
        item.name = this.state.name;
        item.tel = this.state.tel;
        item.permisson = this.state.permisson;
    }
    Kiemtratrangthai = () => {
        if (this.props.Action === true) {
            return (
                <div className="col">
                    <form>
                        <div className="card border-primary mb-3 mt-2">
                            <div className="card-header">Thêm Mới Thành Viên</div>
                            <div className="card-body text-primary">
                                <div className="card-title" />
                                <div className="form-group">
                                    <label>Tên User:</label>
                                    <input type="text" onChange={(event) => this.isChange(event)} name="name" className="form-control" placeholder="Nhap user..." />
                                    <label>Điện Thoại:</label>
                                    <input type="text" onChange={(event) => this.isChange(event)} name="tel" className="form-control" placeholder="Nhap ĐT..." />
                                    <label>Chức Vụ:</label>
                                    <div className="input-group">
                                        <select className="custom-select" onChange={(event) => this.isChange(event)} name="permisson" id="inputGroupSelect04" aria-label="Example select with button addon">
                                            <option selected>Chọn Quyền Mặc Định</option>
                                            <option value={1}>Admin</option>
                                            <option value={2}>Modrator</option>
                                            <option value={3}>Normal</option>
                                        </select>
                                    </div>
                                    <br />
                                    <div className="form-group">
                                        < input type="reset" className="btn btn-block btn-primary" onClick={(name, tel, permisson) => this.props.add(this.state.name, this.state.tel, this.state.permisson)} value="Thêm Mới"/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            );
        } else {

        }
    }
    render() {
        return (
            <div>
                {this.Kiemtratrangthai()}
            </div>
        );
    }
}

export default AddUser;