import React, { Component } from 'react'

export default class TableDataRow extends Component {

    PermissonShow = () => {
        if (this.props.permisson === 1) {
            return "admin";
        } else if (this.props.permisson === 2) {
            return "Moderator";
        } else {
            return "Normal User";
        }
    }
    editClick = () => {
        this.props.editFunClick();
        this.props.changeEitUserStatus();
    }
    render() {
        return (
            <tr>
                <td >{this.props.stt + 1}</td>
                <td>{this.props.username}</td>
                <td>{this.props.sodt}</td>
                <td>{this.PermissonShow()}</td>
                <td>
                    <div className="btn-group">
                        <div className="btn btn-warning sua" onClick={() => this.editClick()}><i className="fa fa-edit" />
              Sữa</div>
                        <div className="btn btn-danger xoa"><i className="fa fa-edit" />
              Xóa</div>
                    </div>
                </td>
            </tr>
        );
    }
}
