import React, { Component } from 'react';
import TableDataRow from './TableDataRow';

class TableData extends Component {
  
    MapDataUser = () => this.props.Dulieu.map((value, key) => (
        <TableDataRow changeEitUserStatus={() => this.props.changeEitUserStatus()}
        editFunClick={(user) => this.props.editFun(value)} stt={key} username={value.name} sodt={value.tel} quanly={value.permisson} />
    ))
    render() {
        return (
            <div className="col">
                <table className="table table-striped table-inverse table-hover">
                    <tbody>
                       {this.MapDataUser()}
                    </tbody>
                </table>
            </div>
        );
    }
}

export default TableData;