import React, { Component } from 'react'

export default class EditUser extends Component {
    constructor(props){
        super(props);
        this.state = {
            id: this.props.userEditObject.id,
            name: this.props.userEditObject.name,
            tel: this.props.userEditObject.tel,
            permisson: this.props.userEditObject.permisson,
        }
    }

    isChange = (event) =>{
       const name = event.target.name;
       const value = event.target.value;
       this.setState({
           [name]:value
       })
    }
    saveButton = () => {
        var info = {};
        info.id = this.state.id;
        info.name = this.state.name;
        info.tel = this.state.tel;
        info.permisson = this.state.permisson;
        this.props.getUserEditInfo(info);
        this.props.changeEditUserStatus();
    }
    render() {
        console.log(this.state);
        return (
            <div className="col-12">
                <form method="post">
                    <div className="card text-while bg-waring mb-3 mt-2">
                        <div className="card-header text-center">Sữa Thông Tin User</div>
                        <div className="card-body text-primary">
                            <div className="card-title" />
                            <div className="form-group">
                                <label>Tên User:</label>
                                <input onChange={(event) => this.isChange(event)}
                                    defaultValue={this.props.userEditObject.name} type="text" onChange={(event) => this.isChange(event)} name="name" className="form-control" placeholder="Nhap user..." />
                                <label>Điện Thoại:</label>
                                <input onChange={(event) => this.isChange(event)}
                                    defaultValue={this.props.userEditObject.tel} type="text" onChange={(event) => this.isChange(event)} name="tel" className="form-control" placeholder="Nhap ĐT..." />
                                <label>Chức Vụ:</label>
                                <div className="input-group">
                                    <select onChange={(event) => this.isChange(event)}
                                    defaultValue={this.props.userEditObject.permisson}  className="custom-select" onChange={(event) => this.isChange(event)} name="permisson" id="inputGroupSelect04" aria-label="Example select with button addon">
                                        <option selected>Chọn Quyền Mặc Định</option>
                                        <option value={1}>Admin</option>
                                        <option value={2}>Modrator</option>
                                        <option value={3}>Normal</option>
                                    </select>
                                </div>
                                <br />
                                <div className="form-group">
                                    <input type="button" className="btn btn-block btn-danger" value="Save" onClick={() => this.saveButton()} />
                                </div>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}
