import React, { Component } from 'react';
import EditUser from './EditUser';

class Search extends Component {
    constructor(props){
        super(props);
        this.state = {
            tempValue: '',
            userObj: {}
        }
    }
    getUserEditInfo = (info) =>{
          this.setState({
              userObj: info
          })
    }
    
    isShowEditForm = () =>{
        if (this.props.editUserStatus === true) {
            return <EditUser getUserEditInfo={(info) => this.getUserEditInfo(info)} userEditObject={this.props.userEditObject}
             changeEditUserStatus={()=> this.changeEditUserStatus()}/>;
        }
    }
    isChange = (event) =>{
        this.setState({
            tempValue: event.target.value
        });
    }
     Hienthiform = () => {
         if(this.props.Show === true){
             return (<div className="btn btn-block btn-info" onClick={() => this.props.Ketnoi()}> Đóng Lại </div>);
         } else{
           return  (<div className="btn btn-block btn-info" onClick={() => this.props.Ketnoi()}> Thêm Mới </div>);
         }
     }
    render() {
        return (
            <div className="col-12">
                {this.isShowEditForm()}
                <div className="col-12">
                    <div className="form-group">
                        <div className="btn-group">
                            <input type="text" className="form-control" onChange={(event) => this.isChange(event)}  placeholder="Nhập từ khóa..." style={{ width: '600px' }} />
                            <div className="btn btn-info" style={{ width: '105px' }}  onClick={(dl) => this.props.Connect(this.state.tempValue)}>Tìm</div>
                        </div>
                    </div>
                    <div className="btn-group1">
                        {this.Hienthiform()}
                    </div>
                </div>
                <div className="col-12">
                    <hr />
                </div>
            </div>
        );
    }
}

export default Search;