import React, { Component } from 'react';
import './../App.css';
import Header from './Header';
import Search from './Search';
import TableData from './TableData';
import AddUser from './AddUser'
import DataUser from './Data.json';
import { v4 as uuidv4 } from 'uuid';

class App extends Component {
    state = {
        Hienthiform: false,
        Data: DataUser,
        searchText: '',
        editUserStatus: false,
    };
    constructor(props) {
        super(props);
        this.state = {
            Hienthiform: false,
            Data: DataUser,
            searchText: '',
            editUserStatus: false,
            userEditObject: {}
        }
    }
    changeEitUserStatus = () =>{
        this.setState ({
            editUserStatus: !this.state.editUserStatus
        });
    }
    getNewData = (name, tel, permisson) => {
        var item = {};
        item.id = uuidv4();
        item.name = name;
        item.tel = tel;
        item.permisson = permisson;
        var items = this.state.Data;
        items.push(item);
        
        this.setState({
            Data:items,
        });
    }
    editUser = (user) => {
        console.log("Kết Nối Thành Công!");
        this.setState({
            userEditObject: user
        })
        console.log(user);
    }
    Doitrangthai = () => {
        this.setState({
            Hienthiform:!this.state.Hienthiform
        });
    }
    getTextSearch = (dl) => {
        this.setState({
            searchText: dl
        })
    }
    render() {
        var ketqua = [];
        this.state.Data.forEach((item) => {
            if (item.name.indexOf(this.state.searchText) !== -1) {
                ketqua.push(item);
            }
        })
        return (
            <div className="container" >
                <div className="row" >
                    <Header />
                    <Search userEditObject={this.state.userEditObject}
                    Connect={(dl) => this.getTextSearch(dl)}
                        Ketnoi={() => this.Doitrangthai()} 
                        Show={this.state.Hienthiform} 
                        editUserStatus={this.state.editUserStatus}
                        changeEitUserStatus={() => this.changeEitUserStatus()}
                        />
                    <TableData changeEitUserStatus={() =>this.changeEitUserStatus()}
                     editFun={(user) => this.editUser(user)} Dulieu={ketqua} />
                    <AddUser add={(name, tel, permisson) => this.getNewData(name, tel, permisson)} Action={this.state.Hienthiform} />
                </div>
            </div>
        );
    }
}

export default App;
